import Vue from 'vue'
import App from './App.vue'
import Post from './Post.vue'
import List from './List.vue'
import Categories from './Categories.vue'
import Vocabulary from './Vocabulary.vue'
import Progress from './Progress.vue'
// import Loading from './Loading.vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

Vue.component('list', List);
Vue.component('post', Post);
Vue.component('categories', Categories);
Vue.component('vocabulary', Vocabulary);
Vue.component('progress', Progress);

// Vue.component('loading', Loading)

// 0.     (. vue-cli),
//  Vue  VueRouter    `Vue.use(VueRouter)`

// 1.   
//       
// const Post = { template: '<div> {{ $route.params.id }}</div>' }


// 2.  
//      
// ""      `Vue.extend()`
//  ,       
//     .
const routes = [
  { path: '/', component: List},
  { path: '/post/:id', component: Post},
  { path: '/categories/', component: Categories},
  { path: '/vocabulary/', component: Vocabulary},
  { path: '/progress/', component: Progress},
]

// 3.      `routes`
//     ,     
const router = new VueRouter({
  routes //   `routes: routes`
})



// 4.      Vue  .
// ,       `router`,
//       
const app = new Vue({
  el: '#app',
  router,
  render: h => h(App, List, Post, Categories, Vocabulary, Progress)
})

